  --bg-j: 34, 197, 94;
  --bg-k: 22, 163, 74;
  --bg-l: 67, 56, 202;
  --bg-m: 242, 255, 0;
  --br-a: #e4e4e7;
  --br-b: #f4f4f5;
  --br-c: #d4d4d8;
