.user-avatar {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.5rem;
  background-color: rgba(var(--bg-d), 1);
  border-radius: 50%;
  font-size: 1.5rem;
  height: 3rem;
  width: 3rem;
}

.sign-out {
  border-radius: 0.375rem;
  padding: 0.5rem;
  color: rgba(var(--cl-d), 1);
}

.danger {
  background-color: rgba(var(--bg-i), 1);
  color: rgba(var(--cl-a), 1) !important;
  font-size: 0.75rem !important;
  font-weight: 700 !important;
  padding: 0.25rem 0.4rem !important;
  line-height: 1.5;
  width: fit-content;
  height: fit-content;
  border-radius: 0.357rem;
}

.success {
  background-color: rgba(var(--bg-j), 1);
  color: rgba(var(--cl-a), 1) !important;
  font-size: 0.75rem !important;
  font-weight: 700 !important;
  padding: 0.25rem 0.4rem !important;
  line-height: 1.5;
  border-radius: 0.357rem;
  width: fit-content;
  height: fit-content;
}

.button-success {
  color: rgba(var(--cl-a), 1);
  background: rgba(var(--bg-j), 1);
  border: solid 1px var(--br-f);
  border-radius: 0.375rem;
  font-family: var(--font-family);
  font-size: 1rem;
  padding: 0.75rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-success:hover {
  background: rgba(var(--bg-k), 1);
  border-color: var(--br-g);
}
.button-success:focus {
  box-shadow: 0 0 0 2px #fff, 0 0 0 4px #22c55e, 0 1px 2px 0 transparent;
  outline: 0 none;
  outline-offset: 0;
}

.button-danger {
  background-color: transparent;
  border: 1px solid var(--br-h);
  color: rgba(var(--bg-i), 1);
  border-radius: 0.375rem;
  font-family: var(--font-family);
  font-size: 1rem;
  padding: 0.75rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-danger:hover {
  background: rgba(239, 68, 68, 0.04);
  border: 1px solid;
}
.button-danger:focus {
  box-shadow: 0 0 0 2px #fff, 0 0 0 4px #ef4444, 0 1px 2px 0 transparent;
  outline: 0 none;
  outline-offset: 0;
}

.button-sumbit {
  color: rgba(var(--cl-a), 1);
  background: rgba(var(--bg-g), 1);
  border: solid 1px var(--br-e);
  border-radius: 0.375rem;
  font-family: var(--font-family);
  font-size: 1rem;
  padding: 0.75rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-sumbit:hover {
  background: rgba(var(--bg-l), 1);
  border-color: var(--br-i);
}
.button-sumbit:focus {
  box-shadow: 0 0 0 2px #fff, 0 0 0 4px #6366f1, 0 1px 2px 0 transparent;
  outline: 0 none;
  outline-offset: 0;
}

.sign-out {
  border-radius: 0.375rem;
  padding: 0.5rem !important;
  background: transparent;
  height: 2.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(var(--cl-d), 1);
}
.sign-out:hover {
  background: rgba(var(--bg-c), 1);
  color: rgba(var(--cl-c), 1);
}

.main-nav {
  display: flex;
  grid-gap: 0.25rem;
  align-items: center;
}
.main-nav__button {
  color: rgba(var(--cl-d), 1);
  background-color: transparent;
  transition: all 0.4s;
  border-radius: 0.375rem;
  padding: 0.5rem 1rem;
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  font-weight: 500;
  text-decoration: none;
  overflow: hidden;
  position: relative;
}
.main-nav__button:hover:not(.active) {
  color: rgba(var(--cl-c), 1);
  background-color: rgba(var(--bg-c), 1);
}
.main-nav__button.active {
  color: rgba(var(--cl-c), 1);
  background-color: rgba(var(--bg-c), 1);
}

.statistic-list {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1024px) {
  .statistic-list {
    min-height: 14.05rem;
    overflow-x: scroll;
  }
}
.statistic-list__row {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(10, 1fr);
  border: solid var(--br-b);
  border-width: 0 0 1px;
  text-align: left;
  background: rgba(var(--bg-a), 1);
  position: relative;
}
.statistic-list__row::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  bottom: -0.75rem;
  left: 0;
  border-bottom: 1px solid var(--br-b);
}
@media (max-width: 1440px) {
  .statistic-list__row {
    grid-template-columns: 0.3fr 0.2fr 0.2fr 0.3fr repeat(4, 0.25fr) 0.2fr 0.2fr;
  }
}
@media (max-width: 1239px) {
  .statistic-list__row {
    grid-template-columns: 0.17fr 0.1fr 0.12fr 0.2fr repeat(4, 0.12fr) 0.05fr 0.05fr;
  }
}
@media (max-width: 1024px) {
  .statistic-list__row {
    grid-template-columns: repeat(10, 1fr);
  }
}
.statistic-list__row--name {
  grid-gap: 0;
  margin-bottom: 0.75rem;
}
@media (max-width: 1024px) {
  .statistic-list__row--name {
    grid-template-columns: 111px 62px 72px 115px repeat(4, 77px) repeat(2, 32px);
  }
}
.statistic-list__coll {
  font-family: var(--font-family);
  font-size: 1rem;
  font-weight: 400;
  color: rgba(var(--cl-e), 1);
  display: flex;
  align-items: center;
  text-align: start;
  padding: 0.5rem 0.75rem;
  transition: all 0.4s;
}
.statistic-list__name {
  color: rgba(var(--cl-g), 1);
  font-weight: 500;
  background: rgba(var(--bg-f), 1);
  padding: 0.375rem 0.75rem;
}

.proxy-list {
  max-height: 500px;
  position: relative;
  overflow: auto;
}
.proxy-list .statistic-list__row {
  grid-template-columns: repeat(6, 1fr);
}
.proxy-list .statistic-list__coll {
  padding: 0.73rem 0.75rem;
}

[data-balance]:hover {
  color: rgba(var(--cl-b), 1);
  background-color: rgba(var(--bg-f), 1);
}

.search-form {
  box-shadow: 0 0 rgba(0, 0, 0, 0), 0 0 rgba(0, 0, 0, 0), 0 1px 2px rgba(0, 0, 0, 0.0509803922);
}
.search-form__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0.75rem;
}
.search-form__btn svg {
  fill: transparent;
  stroke: rgba(var(--cl-e), 0.7);
}
.search-form__label {
  appearance: none;
  background: rgba(var(--bg-a), 1);
  border: 1px solid var(--br-c);
  border-radius: 0.375rem;
  padding: 0.75rem;
  position: relative;
  padding-left: 2.5rem;
}
